import babyApi from "../../service/baby";

export default {
	data() {
		return {
			currentDate: new Date().getTime(),
			minDate: new Date().getTime(),
			formatter(type, value) {
			  if (type === 'year') {
				return `${value}年`;
			  } else if (type === 'month') {
				return `${value}月`;
			  }
			  return value;
			},
			backUrl:"",
			isShow: false,
		}
	},
	methods: {
		onChange(){
			this.isShow = true;
		},
		onInput(event) {
			this.setData({
			  currentDate: event.detail
			});
		},
		savebabyBtn(){
				let obj={
					"birthday": "2019-11-24",
					"sex": 0
				};
				babyApi.babySave(obj).then(res=>{
					if(res.code == "0000"){
						console.log(this.backUrl)
						uni.switchTab({
						    url: this.backUrl || '/pages/task/index'
						});
					}else{
						uni.showToast({
							icon:"none",
						    title: res.responseMsg
						});
					}
				});
		}
	},
	onLoad(option){
		if(option.backpath){
			this.backUrl = option.backpath;
		}
	}
}
